@mixin flexCenter($direction, $justify) {
    display: flex;
    justify-content: $justify;
    align-items: center;    
    flex-direction: $direction;
}


header {

    @include flexCenter(column, space-evenly);
    background-color: rgba(0, 0, 0, 0.582);
    height: 100px;
    width: 100%;
    text-align: center;

    nav {
        @include flexCenter(row, space-evenly); 
        list-style: none;
        width: 50%;
        
        li {
            margin: 0 14px 20px;
        }

        h2 {
            margin: 0;
        }
   }

   .header-logo {
       @include flexCenter(row, center);
       
       img {
           width: 50%;
           color: white;
       }
   }
}
