@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;500;700&display=swap);
header{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;background-color:rgba(0,0,0,0.582);height:100px;width:100%;text-align:center}header nav{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row;list-style:none;width:50%}header nav li{margin:0 14px 20px}header nav h2{margin:0}header .header-logo{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}header .header-logo img{width:50%;color:white}.home-container{background:linear-gradient(135deg, #1b1b2f 7%, #1f4068 48%, #e43f5a 100%);height:100vh;color:white}.home-container .home-body-container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;text-align:center}body{margin:0;font-family:'Ubuntu', sans-serif}

